}

.top-content:before, .top-content:after {
    content: " ";
    display: table;
}

.top-content:after {
    clear: both;
}

.academic .image {
    background: #f4f4f4;
    margin-bottom: 20px;
}

.image-details,
.brief-info,
.top-info {
