    }
}

.photo {
    margin: 0 auto;
    overflow: auto;
    text-align: center;
}

.photo img {
    background: #fff;
    max-height: 200px;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
}

.photo i {
    font-size: 120px;
    color: #ccc;
    line-height: 200px;
}

.short-description {
    overflow: visible;
}

.academic h1 {
    margin: 0;
    line-height: 1em;
}

.job-title {
    clear: left;
    font-family: ff-meta-web, 'Helvetica Neue', Arial, Helvetica, sans-serif;
    font-weight: 300;
    line-height: 1.4em;
    font-size: 1.6em;
    color: #666;
    margin: 0 0 10px 0;
}

.who .job-title {
    color: #000;
    font-weight: normal;
}

.person-meta {
    margin: 0 0 10px 0;
}

.business-unit-list {
    list-style: none;
    width: 100%;
}

.business-unit-list li {
    display: inline;
