    border-right: 1px solid #ccc;
}

.business-unit-list li:first-child {
    margin-left: 0;
}

.business-unit-list li:last-child {
    padding-right: 0;
    border: none;
}

.contact-toggler {
    display: inline-block;
    width: 100%;
    text-align: center;
    margin: 0;
}

.contact-toggler a {
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #f5f5f5;
    background-image: linear-gradient(to bottom, #f5f5f5 0%, #f1f1f1 100%);
    color: #444;
    border: 1px solid #dcdcdc;
    border: 1px solid rgba(0, 0, 0, 0.1);
    font-weight: bold;
    padding: 10px 20px;
    display: block;
}

.contact-toggler a:hover {
    border: 1px solid #c6c6c6;
}

.contact-toggler a:active {
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
}

.contact-toggler a:focus {
    border: 1px solid #4d90fe;
}

.contact-toggler.show a {
    background-image: none;
    background-color: #f6f6f6;
    -webkit-border-top-left-radius: 4px;
    -webkit-border-top-right-radius: 4px;
    -moz-border-radius-topleft: 4px;
    -moz-border-radius-topright: 4px;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}

.contact,
.expertise {
    background-color: #f9f9f9;
}

.contact h2,
.expertise h2 {
    font-size: 1.2em;
}

.contact > h2,
.expertise > h2 {
    margin-left: 20px;
}

.contact a:hover,
.expertise a:hover {
    border-color: #f9f9f9;
}

.contact {
    padding: 20px 0;
}

.contact:before, .contact:after {
    content: " ";
    display: table;
}

.contact:after {
    clear: both;
}

.contact h2 {
    margin-bottom: 20px;
}

.js #contact {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

.js #contact.show {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: 100%;
}

.js #contact.focusable:active,
.js #contact.focusable:focus {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto;
}

.contact-list:before, .contact-list:after {
    content: " ";
    display: table;
}

.contact-list:after {
    clear: both;
}

