    height: auto;
}

.academic-listing .academic .image {
    width: 21%;
    margin-right: 1em;
    height: auto;
    float: left;
    text-align: center;
}

.academic-listing .academic .image:before, .academic-listing .academic .image:after {
    content: " ";
    display: table;
}

.academic-listing .academic .image:after {
    clear: both;
}

.academic-listing .academic .image a {
    display: block;
    border-bottom: none;
}

.academic-listing .academic .image img {
    margin: 0;
    max-height: 100px;
    display: inline-block;
    vertical-align: middle;
}

.academic-listing .academic .image span {
    font-size: 500%;
    display: inline-block;
    color: #ccc;
    width: 100%;
}

.academic-listing .academic .who {
    float: left;
    width: 75%;
}

.academic-listing .academic .who h2 {
    margin-top: 0;
}

.academic-listing .academic .meta {
    float: left;
    width: 75%;
    padding: 0;
}

.academic-listing h2 a {
    border-bottom: 1px solid #ccc;
    font-weight: bold;
}

.academic-listing h2 a:hover {
    border-color: #f6f6f6;
}

.academic-listing h2 a:visited {
    color: #b02a1a;
}

.academic-listing h2 a strong {
    font-weight: bold;
}

.scroll-btn {
    height: 40px;
    width: 40px;
    border: 1px solid #ccc;
    background: #fefefe;
    -webkit-backface-visibility: hidden;
    transform: translateY(80px);
    transition-duration: 0.25s;
    transition-property: transform;
    bottom: -1px;
    position: fixed;
    right: 14px;
    visibility: hidden;
    z-index: 652;
    cursor: pointer;
}

.scroll-btn.visible {
    transform: translateY(0px);
    visibility: visible;
}

#fb-queryform {
    padding: 20px;
    background: #f6f6f6;
    margin-bottom: 20px;
}

#fb-queryform #query {
    background: #fff;
    padding: 7px;
    font-size: 1.2em;
    height: 34px;
    border: 1px solid #ccc;
}

#fb-queryform button {
    padding: 5px;
    background-color: #f8f8f8;
    background-image: linear-gradient(to bottom, #fcfcfc 0, #f8f8f8 100%);
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
    border: 1px solid #ccc;
    color: #b02a1a;
    margin: 0 0 0 -1px;
    height: 34px;
}

#fb-queryform button .fa-search {
    line-height: 22px;
}

#fb-queryform button:hover {
    cursor: pointer;
    background: #f8f8f8;
}

#fb-queryform button:hover i {
    color: #e31b25;
}

#fb-queryform form > div {
    position: relative;
    display: inline-block;
    overflow: hidden;
}

#fb-queryform .clear-btn {
    position: absolute;
    right: 38px;
    top: 20%;
    color: #666;
    cursor: pointer;
    border-bottom: none;
}

#fb-queryform .clear-btn:hover, #fb-queryform .clear-btn:active, #fb-queryform .clear-btn:focus {
    background: white;
    color: #333;
}

#fb-queryform .tag-filter {
    border-radius: 0.3em;
    background: #e3f0fd;
    color: #00739b;
    padding: 0.2em;
    display: inline-block;
    margin-top: 14px;
}

#fb-queryform .tag-filter i {
    padding-left: 0.4em;
}

#fb-queryform .tag-filter:hover {
    cursor: pointer;
}

@media only screen and (min-width: 480px) {
    .contact-list {
        float: left;
        border-right: 1px solid #fff;
        width: 44%;
        padding-left: 20px;
    }
    .contact-list.first {
        border-left: none;
    }
    .contact-list.social {
        border-right: none;
    }
    .contact-list.map {
        border-right: none;
    }
    .contact-list.adr {
        border-left: none;
    }
    .academic .image {
        width: 200px;
        height: 200px;
        float: left;
        margin-right: 20px;
    }
    .contact-toggler {
        width: 200px;
    }
}

@media only screen and (min-width: 600px) {
    .academic-listing .academic .business-unit,
    .academic-listing .academic .keywords {
        display: block;
    }
}

@media only screen and (min-width: 768px) {
    .academic-listing .image {
        width: 21%;
        float: left;
    }
    .academic-listing .who {
        float: left;
        width: 75%;
    }
    .academic-listing .meta {
        float: left;
        width: 75%;
        padding: 0;
    }
    .filters {
        border-right: 1px solid #ccc;
    }
    .filter-list li {
        margin-bottom: 2px;
        float: none;
    }
    #fb-queryform #query {
        width: 300px;
    }
}

@media only screen and (min-width: 980px) {
    .contact-list {
        width: 20%;
    }
    .contact-list.adr {
        border-left: 1px solid #fff;
        clear: none;
    }
    .contact-list.map {
        clear: none;
    }
    .contact-list.social {
        border-right: 1px solid #e7e7e7;
    }
    .academic-listing .academic {
        width: 49%;
        margin-left: 2%;
        min-height: 150px;
        max-height: 150px;
    }
    .academic-listing .academic.odd {
        margin-left: 0;
    }
    .academic-listing li:nth-child(odd) {
        margin-left: 0;
    }
}

@media only screen and (min-width: 1200px) {
    .academic-listing .academic {
        min-height: 130px;
        max-height: 130px;
    }
}
