// Academic expert profile stlyes
// Partial used by academics.css
// Not included in style.css

@mixin loading-info {
    @include loading-fa-icon-content-bg;
}

// move to style.css
h4 {
    font-weight: normal;
    clear: left;
}

// icon colours
.academic i {
    color: #666;
}

.top-content {
    @include clearfix;

    padding: 2.04082%;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    box-shadow: 0 8px 6px -6px rgba(0, 0, 0, 0.2);
}

.academic .image {
    background: #f4f4f4;
    margin-bottom: 20px;
}

.image-details,
.brief-info,
.top-info {
    overflow: hidden;
}

.brief-info__cass-academic-logo {
    text-align: center;
    margin-bottom: 20px;
    img {
        width: 150px;
        max-width: 100%;
    }
    @include breakup-breakpoint('thinish_extended_2') {
        // 600px
        img {
            width: 120px;
        }
        float: right;
    }
}

.photo {
    margin: 0 auto;
    overflow: auto;
    text-align: center;

    img {
        background: #fff;
        max-height: 200px;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
    }

    i {
        font-size: 120px;
        color: #ccc;
        line-height: 200px;
    }
}

.short-description {
    overflow: visible;
}

.academic h1 {
    margin: 0;
    line-height: 1em;
}

.job-title {
    clear: left;
    font-family: ff-meta-web, 'Helvetica Neue', Arial, Helvetica, sans-serif;
    font-weight: 300;
    line-height: 1.4em;
    font-size: 1.6em;
    color: #666;
    margin: 0 0 10px 0;
}

.who .job-title {
    color: #000;
    font-weight: normal;
}

.person-meta {
    margin: 0 0 10px 0;
}

.business-unit-list {
    list-style: none;
    width: 100%;

    li {
        display: inline;
        padding-right: 10px;
        margin-left: 10px;
        border-right: 1px solid #ccc;
    }

    li:first-child {
        margin-left: 0;
    }

    li:last-child {
        padding-right: 0;
        border: none;
    }

    a {
    }
}

.contact-toggler {
    display: inline-block;
    width: 100%;
    text-align: center;
    margin: 0;

    a {
        border: 1px solid #ccc;
        border-radius: 4px;
        background-color: #f5f5f5;
        background-image: linear-gradient(to bottom, #f5f5f5 0%, #f1f1f1 100%);
        color: #444;
        border: 1px solid #dcdcdc;
        border: 1px solid rgba(0, 0, 0, 0.1);
        font-weight: bold;
        padding: 10px 20px;
        display: block;

        &:hover {
            border: 1px solid #c6c6c6;
        }

        &:active {
            box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
        }

        &:focus {
            border: 1px solid #4d90fe;
        }
    }

    &.show {
        a {
            background-image: none;
            background-color: #f6f6f6;
            -webkit-border-top-left-radius: 4px;
            -webkit-border-top-right-radius: 4px;
            -moz-border-radius-topleft: 4px;
            -moz-border-radius-topright: 4px;
            border-top-left-radius: 4px;
            border-top-right-radius: 4px;
        }
    }
}

.contact,
.expertise {
    background-color: #f9f9f9;

    h2 {
        font-size: 1.2em;
    }

    & > h2 {
        margin-left: 20px;
    }

    a:hover {
        border-color: #f9f9f9;
    }
}

.contact {
    padding: 20px 0;

    @include clearfix;

    h2 {
        margin-bottom: 20px;
    }
}

//accessibly hide contact div
.js #contact {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}

.js #contact.show {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: 100%;
}

.js #contact.focusable:active,
.js #contact.focusable:focus {
    clip: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    position: static;
    width: auto;
}

.contact-list {
    @include clearfix;

    ul {
        margin: 0 0 20px 0;
        list-style: none;
    }

    .adr {
        overflow: hidden;
    }

    .icon-map-marker {
        display: block;
        float: left;
        margin: 5px 10px 0 0;
    }

    .website {
        margin-bottom: 2px;
    }

    li {
        padding: 1px 0;
    }

    .email,
    .website,
    .twitter {
        overflow-wrap: break-word;
    }
}

ul.contact-list {
    list-style: none;

    //margin: 0 0 20px 0;
    padding-right: 20px;
}

.contact-list.map {
    clear: left;

    img {
        margin-bottom: 0;
        opacity: 0.9;
        transition: all 0.25s linear;
    }

    &:hover img {
        opacity: 1;
    }
}

.expertise {
    border: 1px solid #ccc;
    margin-bottom: 20px;
    padding: 10px;

    i {
        margin-right: 5px;
    }

    p {
        font-size: 0.9em;
    }
}

#overview {
    ol {
        margin-left: 0;
        li {
            margin-left: 0;
            margin-bottom: 1em;
            list-style: none;
        }
    }
    ul.expertise-list {
