}

#publications #favourites li {
    background: #f9f9f9;
    margin: 1em 0;
    padding: 1em 1em 2em 1em;
    border: 1px solid #ccc;
    position: relative;
    list-style: none;

    .summary {
        margin: 0;
    }

    &::after {
        content: 'Featured Publication';
        position: absolute;
        bottom: 0.5em;
        right: 1em;
