/******* index page styles *******/

// with js hide the initial fb results while we are loading
.js {
    .academic-listing {
        display: none;
    }

    .academic-listing.backbone-ready {
        display: block;
    }

    .initial-loading-indicator {
        height: 100px;
        position: relative;

        @include loading-info;
    }

    .read-more .infinite {
        display: inline-block;
    }

    .fb-page-nav {
        display: none;
    }
}

//pagination
.read-more .infinite {
    display: none;
}

// common filter styles
.filter {
    border-radius: 3px;
    color: #00739b;
    padding: 2px 5px;

    &.selected {
        border: 1px solid #ccc;
        padding: 2px 5px;
        background: #e3f0fd;
        color: #333;

        &:hover {
            border-color: lighten(#00739b, 20);
        }
    }

    &:focus {
        background-color: #e3f0fd;
    }

    &:visited {
        color: #00739b;
    }
}

// scoped by .js as selected filters have different behaviour with js enabled
.js .filter {
    &.selected {
        &::after {
            content: '\f00d';
            text-indent: -99999px;
            font-family: FontAwesome;
            font-weight: normal;
            font-style: normal;
            text-decoration: inherit;
            -webkit-font-smoothing: antialiased;
            display: inline;
            width: auto;
            height: auto;
            line-height: normal;
            vertical-align: baseline;
            background-image: none;
            background-position: 0% 0%;
            background-repeat: repeat;
            margin-top: 0;
            color: #ccc;
            margin-left: 8px;
        }

        &:hover {
            &::after {
                color: lighten(#00739b, 20);
            }
        }
    }
}

// left col filters
.filters {
    h2 {
        margin: 0 0 10px 0;
        font-size: 1.2em;
        padding-left: 5px;

        i {
            color: #999;
            font-size: 0.9em;
            margin-right: 5px;
        }
    }

    padding: 0 20px 0 0;
}

.filter-list {
    list-style: none;
    margin: 0 0 20px 0;

    @include clearfix;

    li {
        margin-bottom: 2px;
        float: left;
        margin-right: 10px;
    }

    .filter {
        padding: 2px 5px;
        display: block;
        border: none;
        color: #00739b;

        &:hover,
        &:focus {
            background: #e3f0fd;
        }
    }
}

// results summary
.results-filter {
    display: block;
    font-family: Georgia, serif;
    font-size: 1.2em;
}

.academic-listing {
    list-style: none;
    font-size: 0.9em;
    padding: 0;
    margin: 0;

    @include clearfix;

    position: relative;

    &.loading {
        @include loading-info;
    }

    .filter {
        color: #00739b;
        border-radius: 3px;
        border: none;
        padding: 2px;

        &.selected {
            background: #e3f0fd;
            color: #00739b;

            &:hover {
                border-color: lighten(#00739b, 10);
            }
        }

        &:visited {
            color: #00739b;
        }

        &:hover {
        }

        &:active {
        }

        &:focus {
        }

        &::after {
            content: ',';
        }

        &:last-child {
            &::after {
                content: '';
            }
        }
    }

    .business-unit {
        margin: 0;

        .icon-building {
        }
    }

    .academic {
        @include clearfix;

        width: 100%;
        float: left;
        margin: 0 0 2% 0;
        padding: 1em;
        border-radius: 3px;
        position: relative;
        background: #f9f9f9;
        list-style: none;
        border: 1px solid #d8d8d8;
        -webkit-box-sizing: border-box;

        /* Safari/Chrome, other WebKit */
        -moz-box-sizing: border-box;

        /* Firefox, other Gecko */
        box-sizing: border-box;

        /* Opera/IE 8+ */

        p {
            margin-bottom: 5px;
        }

        h2 {
            font-size: 1em;
            margin-bottom: 5px;
            font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif;
        }

        .job-title {
            font-style: normal;
            font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif;
            font-size: 1em;
        }

        &:hover,
        &:focus {
            height: auto;
        }

        .business-unit,
        .keywords {
        }

        .image {
            width: 21%;
            margin-right: 1em;
            height: auto;
            float: left;

            @include clearfix;

            text-align: center;

            a {
                display: block;
                border-bottom: none;
            }

            img {
                margin: 0;
                max-height: 100px;
                display: inline-block;
                vertical-align: middle;
            }

            span {
                font-size: 500%;
                display: inline-block;
                color: #ccc;
                width: 100%;
            }
        }

        .who {
            float: left;
            width: 75%;

            h2 {
                margin-top: 0;
            }
        }

        .meta {
            float: left;
            width: 75%;
            padding: 0;
        }
    }

    h2 a {
        border-bottom: 1px solid #ccc;
        font-weight: bold;

        &:hover {
            border-color: #f6f6f6;
        }

        &:visited {
            color: $red;
        }

        strong {
            font-weight: bold;
        }
    }

    .image {
    }
}

.scroll-btn {
    height: 40px;
    width: 40px;
    border: 1px solid #ccc;
    background: #fefefe;
    -webkit-backface-visibility: hidden;
    -webkit-transform: translateY(80px);
    -moz-transform: translateY(80px);
    -ms-transform: translateY(80px);
    -o-transform: translateY(80px);
    transform: translateY(80px);
    -webkit-transition-duration: 0.25s;
    -moz-transition-duration: 0.25s;
    transition-duration: 0.25s;
    -webkit-transition-property: -webkit-transform, -moz-transform, -o-transform,
        -webkit-transform;
    -moz-transition-property: -webkit-transform, -moz-transform, -o-transform,
        -moz-transform;
    transition-property: -webkit-transform, -moz-transform, -o-transform,
        transform;
    bottom: -1px;
    position: fixed;
    right: 14px;
    visibility: hidden;
    z-index: 652;
    cursor: pointer;

    &.visible {
        -webkit-transform: translateY(0px);
        -moz-transform: translateY(0px);
        -ms-transform: translateY(0px);
        -o-transform: translateY(0px);
        transform: translateY(0px);
        visibility: visible;
    }
}

#fb-queryform {
    padding: 20px;
    background: #f6f6f6;
    margin-bottom: 20px;

    #query {
        background: #fff;
        padding: 7px;
        font-size: 1.2em;
        height: 34px;
        border: 1px solid #ccc;
    }

    button {
        padding: 5px;
        background-color: #f8f8f8;
        background-image: linear-gradient(to bottom, #fcfcfc 0, #f8f8f8 100%);
        text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
        border: 1px solid #ccc;
        color: $red;
        margin: 0 0 0 -1px;
        height: 34px;

        .fa-search {
            line-height: 22px;
        }

        &:hover {
            cursor: pointer;
            background: #f8f8f8;

            i {
                color: $logo-red;
            }
        }
    }

    // for the tag filters
    form > div {
        position: relative;
        display: inline-block;
        overflow: hidden;
    }

    .clear-btn {
        position: absolute;
        right: 38px;
        top: 20%;
        color: #666;
        cursor: pointer;
        border-bottom: none;

        &:hover,
        &:active,
        &:focus {
            background: white;
            color: #333;
        }
    }

    .tag-filters {
        //position: absolute;
        //padding: 0.25em 0.5em;
        //top: 0;
        //left: 0;
    }

    .tag-filter {
        border-radius: 0.3em;
        background: #e3f0fd;
        color: #00739b;
        padding: 0.2em;
        display: inline-block;
        margin-top: 14px;

        i {
            padding-left: 0.4em;
        }

        &:hover {
            cursor: pointer;
        }
    }
}

//landscape mobile and above
@include breakup-breakpoint('thinish') {
    // 480px
    .contact-list {
        float: left;

        //border-left: 1px solid #e7e7e7;
        border-right: 1px solid #fff;
        width: 44%;
        padding-left: 20px;

        &.first {
            border-left: none;
        }

        &.social {
            border-right: none;
        }

        &.map {
            border-right: none;
        }

        &.adr {
            border-left: none;

            //clear:left;
        }
    }

    .academic .image {
        width: 200px;
        height: 200px;
        float: left;
        margin-right: 20px;
    }

    .contact-toggler {
        width: 200px;
    }
}

@include breakup-breakpoint('thinish_extended_2') {
    // 600px
    .academic-listing {
        .academic {
            .business-unit,
            .keywords {
                display: block;
            }
        }
    }
}

//portrait tablets
@include breakup-breakpoint('medium') {
    // 767px
    .academic-listing {
        .academic {
        }

        .image {
            width: 21%;

            //margin: 2%;
            float: left;
        }

        .who {
            float: left;
            width: 75%;
        }

        .meta {
            float: left;
            width: 75%;
            padding: 0;
        }
    }

    .filters {
        border-right: 1px solid #ccc;
    }

    .filter-list {
        li {
            margin-bottom: 2px;
            float: none;
        }
    }

    #fb-queryform {
