            width: 300px;
        }
    }
}

@include breakup-breakpoint('wide') {
    // 980px
    .contact-list {
        width: 20%;

        &.adr {
            border-left: 1px solid #fff;
            clear: none;
        }

        &.map {
            clear: none;
        }

        &.social {
            border-right: 1px solid #e7e7e7;
        }
    }

    .academic-listing {
        .academic {
            width: 49%;
            margin-left: 2%;
            min-height: 150px;
            max-height: 150px;

            &.odd {
                margin-left: 0;
            }
        }

        li:nth-child(odd) {
            margin-left: 0;
        }
    }
}

@include breakup-breakpoint('wider') {
    // 1200px
    .academic-listing {
        .academic {
            min-height: 130px;
            max-height: 130px;
        }
    }
}
